header {
    border-bottom: 1px solid #e0e0e0;
}

.nav-bar {
    position: relative;
    height: 63px;
    width: 100%;
    margin-top: 0;
    color: #616161;
    box-shadow: 0 3px 4px rgb(0 0 0 / 18%);
    border-bottom: 1px solid #e0e0e0;

    h2 {
        height: 63px;
        line-height: 63px;
        float: left;
        font-size: 18px;
        font-weight: 400;
        color: #424242;
    }

    .appraise {
        float: right;
        line-height: 63px;
        color: #616161;
        font-size: 14px;
        cursor: pointer;

        &:hover {
            color: #ff6700;
        }
    }
}

.product-wrap {
    display: flex;
    justify-content: space-around;
    padding-top: 32px;

    .container {
        display: flex;
        justify-content: space-around;

        .inner-left {
            width: 50%;
            float: left;

            img {
                width: 100%;
            }
        }

        .inner-right {
            width: 50%;
            float: right;
            padding-left: 20px;

            .product-name {
                margin: 0;
                padding: 0;
                font-size: 24px;
                font-weight: 400;
                color: #212121;
            }

            .product-desc {
                color: #b0b0b0;
                margin: 0;
                padding: 0;
                padding-top: 8px;
                line-height: 1.5;
            }

            h3 {
                font-size: 18px;
                line-height: 1;
                color: #ff6700;
                margin-top: 14px;
                font-weight: 400;
            }

            .product-price {
                font-size: 18px;
                line-height: 1;
                color: #ff6700;
                padding: 12px 0 10px 0;
            }
        }

        .activity {
            padding-top: 24px;
            border-bottom: 1px solid #e0e0e0;
            border-top: 1px solid #e0e0e0;

            .activity-gift {
                margin-bottom: 24px;
                height: 25px;
                background-color: #ff6700;
                color: white;
                text-align: center;
                width: 80px;
                padding: 2px;
                font-size: 14px;
                line-height: 25px;

            }
        }

        .address {
            padding: 30px 50px;
            line-height: 1.5em;
            margin: 20px 0;
            background: #fafafa;
            border: 1px solid #e0e0e0;

            .address-inner {
                span {
                    margin-right: 14px;
                    font-size: 14px;
                }
            }

            .change {
                font-size: 14px;
                color: #ff6700;
            }

            p {
                font-size: 14px;
                color: #ff6700;
            }
        }

        .addcart {
            width: 300px;
            height: 54px;
            line-height: 54px;
            font-size: 16px;
            background-color: #f25807;
            border-color: #f25807;
            color: #fff;
            text-align: center;
            margin-top: 55px;
        }
    }


}